import "core-js/modules/es.regexp.exec.js";
import "core-js/modules/es.string.replace.js";
import "core-js/modules/es.string.replace-all.js";
import SafeCode from '@/components/sign/SafeCode.vue';
export default {
  components: {
    SafeCode: SafeCode
  },
  data: function data() {
    return {
      username: '',
      password: '',
      safecode: '',
      mesg: ''
    };
  },
  activated: function activated() {
    this.mesg = '';
  },
  methods: {
    // TODO 对输入长度、特殊字符等进行限制
    // TODO 设置验证码
    fixText: function fixText(e) {
      // 不允许空格
      e.target.value = e.target.value.replaceAll(/\s/g, '');
    },
    login: function login() {
      var _this = this;

      if (this.username.length === 0) {
        this.mesg = '请输入用户名!';
      } else if (this.password.length === 0) {
        this.mesg = '请输入密码!';
      } else if (this.safecode.length === 0) {
        this.mesg = '请输入验证码!';
      } else if (this.safecode.toLowerCase() !== this.$refs.codebox.getCode().toLowerCase()) {
        this.$refs.codebox.refreshCode();
        this.mesg = '验证码错误!';
      } else {
        this.$API.userLogin({
          username: this.username,
          password: this.password
        }, function (message) {
          console.log(message);

          if (message.success) {
            _this.mesg = '登录成功!';

            _this.$router.replace({
              name: 'Master'
            });
          } else if (message.code === 40) {
            _this.mesg = '用户不存在!';
          } else if (message.code === 41) {
            _this.mesg = '密码错误!';
          } else {
            _this.mesg = message.info ? message.info : 'unknown error';
          }

          _this.$refs.codebox.refreshCode();
        });
      }
    }
  }
};